<template lang="pug">
  .user-manage
    .user-list
      label.list-title 用户名
      label.list-title 密&nbsp;&nbsp;&nbsp;&nbsp;码
      .list-element
        component(v-for="(user, index) in userList", :key="index", @click="userSelect(index)")
          label {{user.userName}}
          label {{user.passWord}}
    .new-user(@click="toCreateUser")
    .user-control
      label.user-title 用户名
      input(v-model="selectUser.userName")
      br
      label.user-title 密&nbsp;&nbsp;&nbsp;&nbsp;码
      input(v-model="selectUser.passWord")
      br
      label.user-title 权&nbsp;&nbsp;&nbsp;&nbsp;限
      span.check()
        span.check-box(:style="checkStyle(0)", @click="checkChange(0)")
        label 视频转换
      br
      span.check()
        span.check-box(:style="checkStyle(1)", @click="checkChange(1)")
        label 图片检索
      br
      span.check()
        span.check-box(:style="checkStyle(2)", @click="checkChange(2)")
        label 图像识别
      br
      span.check()
        span.check-box(:style="checkStyle(3)", @click="checkChange(3)")
        label 用户管理
      br
      button(@click = "userChange") {{createUserMode ? "新&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建" : "应&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;用"}}
</template>

<script>
  export default {
    name: 'user_manage',
    props: ['request', 'curUser', 'socket'],
    data () {
      return {
        curIndex: -1,
        createUserMode: false,
        selectUser: {
          id: -1,
          userName: '',
          passWord: '',
          permission: [false, false, false, false]
        },
        userList: []
      }
    },
    watch: {
    },
    mounted () {
      let __this = this
      __this.request.get('/userManage/userList', 'socket')
        .end(function (err, res) {
          if (err || !res.ok) {
            console.log(err)
            __this.$message({
              type: 'error',
              message: '连接服务器失败,请确认权限'
            })
          } else {
            let userList = JSON.parse(res.text)
            userList.forEach(function (user) {
              let tmpUser = {
                id: user.id,
                userName: user.userName,
                passWord: user.passWord,
                permission: [user.videoTransform, user.imageSearch, user.imageDetect, user.userManage]
              }
              __this.userList.push(tmpUser)
            })
            __this.curIndex = 0
            __this.selectUser = JSON.parse(JSON.stringify(__this.userList[0]))
          }
        })
    },
    methods: {
      checkStyle (index) {
        let __this = this
        return __this.selectUser.permission[index] ? {
          'background': 'url("static/image/checked.png") no-repeat center'
        } : {
          'background': 'url("static/image/nocheck.png") no-repeat center'
        }
      },
      checkChange (index) {
        let __this = this
        __this.selectUser.permission.splice(index, 1, !__this.selectUser.permission[index])
      },
      userSelect (index) {
        let __this = this
        __this.createUserMode = false
        __this.curIndex = index
        __this.selectUser = JSON.parse(JSON.stringify(__this.userList[index]))
      },
      toCreateUser () {
        let __this = this
        __this.createUserMode = true
        __this.selectUser = {
          id: -1,
          userName: '',
          passWord: '',
          permission: [false, false, false, false]
        }
      },
      userChange () {
        let __this = this
        if (__this.createUserMode) {
          __this.request.put('/userManage/userList')
            .send({
              jsonString: JSON.stringify({
                userName: __this.selectUser.userName,
                passWord: __this.selectUser.passWord,
                createUser: __this.curUser.userName,
                videoTransform: __this.selectUser.permission[0],
                imageSearch: __this.selectUser.permission[1],
                imageDetect: __this.selectUser.permission[2],
                userManage: __this.selectUser.permission[3]
              })
            })
            .end(function (err, res) {
              if (err || !res.ok) {
                console.log(err)
                __this.$message({
                  type: 'error',
                  message: '连接服务器失败,请确认权限'
                })
              } else {
                let tmpData = JSON.parse(res.text)
                if (tmpData.result) {
                  __this.$message({
                    type: 'success',
                    message: '新建用户  ' + __this.selectUser.userName + '  成功'
                  })
                  __this.selectUser.id = tmpData.id
                  __this.userList.push(JSON.parse(JSON.stringify(__this.selectUser)))
                } else {
                  __this.$message({
                    type: 'error',
                    message: '新建用户失败'
                  })
                  console.log(tmpData.errorMessage)
                }
              }
            })
        } else {
          __this.request.post('/userManage/userList')
            .send({
              jsonString: JSON.stringify({
                id: __this.selectUser.id,
                userName: __this.selectUser.userName,
                passWord: __this.selectUser.passWord,
                modifyUser: __this.curUser.userName,
                videoTransform: __this.selectUser.permission[0],
                imageSearch: __this.selectUser.permission[1],
                imageDetect: __this.selectUser.permission[2],
                userManage: __this.selectUser.permission[3]
              })
            })
            .end(function (err, res) {
              if (err || !res.ok) {
                console.log(err)
                __this.$message({
                  type: 'error',
                  message: '连接服务器失败,请确认权限'
                })
              } else {
                let tmpData = JSON.parse(res.text)
                if (tmpData.result) {
                  __this.userList.splice(__this.curIndex, 1, JSON.parse(JSON.stringify(__this.selectUser)))
                } else {
                  __this.$message({
                    type: 'error',
                    message: '修改用户失败'
                  })
                  console.log(tmpData.errorMessage)
                }
              }
            })
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  @yellow-hover: #feb747;
  .user-manage {
    text-align: center;
    .user-list,.user-control{
      height: 50rem;
      width: 40rem;
      position: absolute;
      background-color: white;
      border-radius: 1rem;
      border: 2px solid black;
      text-align: center;
    }
    .user-list{
      margin: 2rem 0 0 5rem;
      label{
        display: inline-block;
        color: @blue;
        text-align: center;
        width: 15rem;
        &.list-title{
          margin-top: 2rem;
          font-size: 1.6rem;
        }
      }
      .list-element {
        margin: 1rem auto;
        height:43rem;
        width: 35rem;
        overflow-y: auto;
        overflow-x: hidden;
        &::-webkit-scrollbar {
          width: 1.5rem;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 4rem;
          //border:2px solid black;
          //background-color: @yellow;
          background: url("../assets/scrollbar.png") no-repeat center;
        }

        label{
          border-bottom: 2px solid black;
          margin: 1.5rem auto;
          height: 1.8rem;
        }
      }
    }
    .new-user {
      position: absolute;
      width: 81px;
      height: 31px;
      background: url("../assets/new-user-normal.png") no-repeat center;
      cursor: pointer;
      margin: 5rem 0 0 43rem;
      &:hover{
        background: url("../assets/new-user-hover.png") no-repeat center;
      }
    }
    .user-control {
      margin: 2rem 0 0 55rem;
      color: @yellow;
      text-align: left;
      span,label{
        display: inline-block;
        font-size: 1.6rem;
      }
      label{
        text-shadow: 1px 0 2px black, -1px 0 2px black, 0 -1px 2px black, 0 1px 2px black;
      }
      label.user-title {
        &:nth-of-type(1) {
          margin: 5rem 0 0 5rem;
        }
        &:nth-of-type(2), &:nth-of-type(3){
          margin: 5.5rem 0 0 5rem;
        }
      }
      span.check{
        margin: 3rem 0 0 14.3rem;
        &:nth-of-type(1) {
          margin-left: 5rem;
        }
        label {
          color: white;
          margin-left: 2rem;
        }
        span.check-box {
          width: 19px;
          height: 19px;
        }
      }
      input {
        border:none;
        border-bottom: 2px solid black;
        color:@blue;
        outline: none;
        width: 20rem;
        margin: 0 0 0 5rem;
      }
      button {
        width: 25.2rem;
        height: 3.9rem;
        background-color: @yellow;
        border: 2px solid black;
        border-radius: 4px;
        color: white;
        font-size: 1.6rem;
        text-shadow: 1px 0 2px black, -1px 0 2px black, 0 -1px 2px black, 0 1px 2px black;
        margin: 5.2rem 0 0 8rem;
        cursor: pointer;
        &:hover {
          background-color: @yellow-hover;
        }
      }
    }
  }
</style>



