<template lang="pug">
  .login
    label.username-label 用户名
    input.username-input(v-model="username", @keydown.enter="login")
    label.password-label 密码
    input.password-input(type="password", v-model="password", @keydown.enter="login")
    button(@click="login", @keydown.enter="login") 登&nbsp;&nbsp;&nbsp;录
</template>

<script>
  export default {
    name: 'login',
    data () {
      return {
        username: '',
        password: ''
      }
    },
    mounted () {
    },
    methods: {
      login: function () {
        let __this = this
        __this.$superagent.get('/Login')
          .query({
            userName: __this.username,
            passWord: __this.password,
            rememberMe: false
          })
          .end(function (err, res) {
            if (err || !res.ok) {
              console.log(err)
              __this.$message({
                type: 'error',
                message: '连接服务器失败'
              })
            } else {
              let tmpData = JSON.parse(res.text)
              if (tmpData.result) {
                __this.$emit('login-user', {
                  id: tmpData.user[0].id,
                  userName: tmpData.user[0].userName,
                  permission: [tmpData.user[0].videoTransform, tmpData.user[0].imageSearch, tmpData.user[0].imageDetect, tmpData.user[0].userManage]
                })
                __this.$router.push('Index')
              } else {
                __this.$message({
                  type: 'error',
                  message: '登录失败,请检查用户名密码'
                })
                console.log(tmpData.errorMessage)
              }
            }
          })
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  .login{
    width: 60rem;
    height: 36rem;
    margin: 10rem auto;
    background: url('../assets/log.jpg') center no-repeat;
    color: @blue;
    text-align: left;
    font-size: 1.6rem;
    label{
      display: inline-block;
      margin-left: 13.8rem;
      margin-bottom: 1.5rem;
    }
    .username-label{
      margin-top: 9.3rem;
    }
    .password-label{
      margin-top: 2.9rem;
    }
    input{
      margin-left: 13.8rem;
      width: 32.4rem;
      border:none;
      border-bottom: 2px solid black;
      background: none;
      height: 2rem;
      color:@blue;
      outline: none;
    }
    button{
      width: 15.6rem;
      height: 4.2rem;
      border:2px solid black;
      border-radius: 4px;
      background-color: @yellow;
      color:white;
      font-size: 2rem;
      margin: 1.5rem 0 0 22rem;
      text-shadow: 1px 0 2px black, -1px 0 2px black, 0 -1px 2px black, 0 1px 2px black;
      cursor: pointer;
      &:hover {
        background-color: #feb747;
      }
      //-webkit-text-stroke: 1px black;
    }
  }
</style>



