<template lang="pug">
  .image-detect
    .image-list
      .list-search
        button.option-check(:style="checkStyle('house')", @click="options.house = !options.house")
        span.option-name 房屋
        button.option-check(:style="checkStyle('tree')", @click="options.tree = !options.tree")
        span.option-name 树木
        button.option-search(@click="curPage = 1;imageSearch();") 检索
      .list-items
        button.check-button.allcheck-button(:style="smallCheckStyle(-1)", @click="checkItem(-1)")
        label 全选
        button.opera-button(@click="imageDelete") 删除
        button.opera-button 保存
        .list-item(v-for="(image,index) in imageList", :key="index")
          button.check-button(:style="smallCheckStyle(index)", @click="checkItem(index)")
          label(@click="imageDisplay(index)") {{image.name}}
      .image-pagination
        el-pagination(layout="prev, pager, next", :total="imageNum", :page-size="pageSize", :current-page.sync="curPage", @current-change="imageSearch")
    .image-compare
      .image-compare-src
        span.image-title
          span 基准图片
        span
          img(:src="'/imageDetect/image?type=0&id=' + curId")
      .image-compare-dest
        span.image-title
          span 识别图片
        span
          img(:src="'/imageDetect/image?type=1&id=' + curId")
</template>

<script>
  export default {
    name: 'image_detect',
    props: ['request', 'curUser', 'socket'],
    data () {
      return {
        imageNum: 0,
        curPage: 1,
        pageSize: 8,
        allChecked: false,
        curId: 0,
        options: {
          house: true,
          tree: false
        },
        imageList: []
      }
    },
    watch: {
      allChecked () {
        let __this = this
        __this.imageList.forEach((item, index) => {
          item.checked = __this.allChecked
        })
      }
    },
    methods: {
      imageSearch () {
        let __this = this
        __this.request.post('/imageDetect/search')
          .send({
            jsonString: JSON.stringify({
              index: (__this.curPage - 1) * __this.pageSize,
              pageSize: __this.pageSize,
              house: __this.options.house,
              tree: __this.options.tree
            })
          })
          .end(function (err, res) {
            if (err || !res.ok) {
              console.log(err)
              __this.$message({
                type: 'error',
                message: '连接服务器失败'
              })
            } else {
              let tmpData = JSON.parse(res.text)
              if (tmpData.result) {
                __this.imageNum = tmpData.imageNum
                __this.imageList.splice(0)
                tmpData.imageList.forEach((item) => {
                  __this.imageList.push({
                    id: item.id,
                    name: item.name,
                    checked: false
                  })
                })
              }
            }
          })
      },
      imageDelete () {
        let __this = this
        let idList = []
        __this.imageList.forEach((item) => {
          if (item.checked) {
            idList.push(item.id)
          }
        })
        if (idList.length === 0) {
          return
        }
        __this.request.delete('/imageDetect/image')
          .send({
            jsonString: JSON.stringify({
              idList: idList
            })
          })
          .end(function (err, res) {
            if (err || !res.ok) {
              __this.$message({
                type: 'error',
                message: '删除失败'
              })
            } else {
              let tmpData = JSON.parse(res.text)
              if (tmpData.result) {
                __this.$message({
                  type: 'info',
                  message: '已删除'
                })
                __this.allChecked = false
                __this.imageList.splice(0)
                __this.curPage = 1
                __this.imageNum = 0
                __this.imageSearch()
              } else {
                console.log(tmpData.errorMessage)
                __this.$message({
                  type: 'error',
                  message: '删除失败'
                })
              }
            }
          })
      },
      imageDisplay (index) {
        this.curId = this.imageList[index].id
      },
      checkItem (index) {
        let __this = this
        if (index === -1) {
          __this.allChecked = !__this.allChecked
        } else {
          __this.$set(__this.imageList[index], 'checked', !__this.imageList[index].checked)
        }
      },
      checkStyle (option) {
        return this.options[option] ? {
          'background-image': 'url("static/image/checked.png")'
        } : {
          'background-image': 'url("static/image/nocheck.png")'
        }
      },
      smallCheckStyle (index) {
        let __this = this
        if (index === -1) {
          return __this.allChecked ? {
            'background': 'url("static/image/checked-small.jpg") no-repeat center'
          } : {
            'background': 'url("static/image/nocheck-small.jpg") no-repeat center'
          }
        } else {
          return __this.imageList[index].checked ? {
            'background': 'url("static/image/checked-small.jpg") no-repeat center'
          } : {
            'background': 'url("static/image/nocheck-small.jpg") no-repeat center'
          }
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  @yellow-hover: #feb747;
  .image-detect {
    .image-list, .image-compare {
      height: 50rem;
      width: 40rem;
      position: absolute;
      background-color: white;
      border-radius: 1rem;
      text-align: center;
      border: 2px solid black;
    }
    .image-list {
      text-align: left;
      margin: 2rem 0 0 5rem;
      label,span {
        display: inline-block;
      }
      .list-search {
        height: 5rem;
        border-bottom: 2px solid black;
        .option-check {
          width: 1.6rem;
          height: 1.6rem;
          border: none;
          background: no-repeat center;
          margin: 0.2rem 0 0 0;
          outline: none;
          &:nth-of-type(1) {
            margin: 2rem 0 0 4rem;
          }
        }
        span.option-name {
          margin: 0 5rem 0 0.5rem;
          height: 1.6rem;
          color: white;
          text-shadow: 1px 0 1px black, -1px 0 1px black, 0 -1px 1px black, 0 1px 1px black;
        }
        button.option-search {
          background-color: @yellow;
          width: 6.8rem;
          height: 2.2rem;
          border: 2px solid black;
          margin-left: 5.5rem;
          border-radius: 4px;
          color:white;
          cursor: pointer;
          text-shadow: 1px 0 2px black, -1px 0 2px black, 0 -1px 2px black, 0 1px 2px black;
          &:hover {
            background-color: @yellow-hover;
          }
        }
      }
      .list-items {
        color:@blue;
        height: 40.7rem;
        label {
          margin: 0 0 0 1rem;
        }
        .check-button {
          outline: none;
          border: none;
          width: 1.1rem;
          height: 1rem;
          background-size: contain;
        }
        .allcheck-button {
          margin: 2rem 0 0 5rem;
        }
        .opera-button {
          width: 4rem;
          cursor: pointer;
          border: 1px solid @blue;
          outline: none;
          &:nth-of-type(2) {
            color: white;
            background-color: @blue;
            margin-left: 16.2rem;
          }
          &:nth-of-type(3) {
            margin-left: 1.5rem;
            color: @blue;
            background-color: white;
          }
        }
        .list-item {
          height: 2rem;
          width: 30rem;
          border-bottom: 2px solid black;
          margin: 2.5rem 0 0 3.5rem;
          padding-left: 1.5rem;
        }
      }
      .image-pagination {
        margin: 3rem 0 0 0;
      }
    }
    .image-compare {
      margin: 2rem 0 0 55rem;
      text-align: left;
      .image-compare-src, .image-compare-dest {
        span {
          display: inline-block;
        }
        .image-title {
          border: 2px solid black;
          border-right: none;
          width: 3rem;
          height: 6.5rem;
          text-align: center;
          background-color: @yellow;
          word-wrap: break-word;
          span {
            width: 1.6rem;
            color: white;
            text-shadow: 1px 0 1px black, -1px 0 1px black, 0 -1px 1px black, 0 1px 1px black;
          }
          & + span {
            position: absolute;
            img {
              width: 32rem;
              height: 18rem;
              border: 2px solid black;
            }
          }
        }
      }
      .image-compare-src {
        margin: 4rem 0 0 1.2rem;
      }
      .image-compare-dest {
        margin: 16rem 0 0 1.2rem;
      }
    }
  }
</style>



