<template lang="pug">
  .video-transform
    .video-control
      button.video-type-button(@click="video.type = 0", :style="videoTypeButtonStyle(0)") 视频文件
      button.video-type-button(@click="video.type = 1", :style="videoTypeButtonStyle(1)") 实时流
      br
      input.video-address(:placeholder="inputPlaceHolder", v-model.trim="video.videoAddress")
      input#select-file(type="file", style="display:none", @change="getFileAddress")
      span(v-if="video.type === 0")
        button.video-file-button(@click="selectFile") 浏览
        button.video-file-button(@click="openVideo", :disabled="importDisabled") 导入
      span(v-else)
        button.video-stream-button(@click="openVideo", :disabled="importDisabled") 导入
      .video-display
        label 间隔
        input(v-model="video.saveInterval")
        label 帧
        div
          img.video(:src="video.image + video.randomNum", alt="")
    .trans-button(@click="videoTransform")
    .video-list
      label.image-title 图片名
      label.image-title 生成时间
      .image-list
        component(v-for="(image, index) in imageList", :key="index")
          label.image-name {{image.name}}
          label.image-time {{image.createTime}}
          br
</template>

<script>
  export default {
    name: 'video_transform',
    props: ['request', 'curUser', 'socket'],
    data () {
      return {
        errorNum: 0,
        videoProcess: null,
        imageSaveProcess: null,
        importDisabled: false,
        video: {
          type: 0,
          videoAddress: '',
          saveInterval: 10,
          image: '/videoTransform/getVideo',
          randomNum: '?random=2121'
        },
        imageList: []
      }
    },
    mounted () {
      let __this = this
      __this.request.get('/videoTransform/imageList?index=0&pageSize=8')
        .end(function (err, res) {
          if (err || !res.ok) {
            console.log(err)
            __this.$message({
              type: 'error',
              message: '连接服务器失败'
            })
          } else {
            let tmpData = JSON.parse(res.text)
            if (tmpData.result) {
              if (tmpData.isVideoOpen) {
                __this.videoProcess = setInterval(() => {
                  __this.video.randomNum = '?random=' + Math.random()
                }, 100)
              }
              tmpData.imageList.forEach((item) => {
                let tmpImage = {
                  id: item.id,
                  name: item.name,
                  createTime: item.createTime
                }
                __this.imageList.push(tmpImage)
              })
            } else {
              __this.$message({
                type: 'error',
                message: '获取图片列表失败'
              })
              console.log(tmpData.errorMessage)
            }
          }
        })
    },
    watch: {
      'video.type': function () {
        this.video.address = ''
      }
    },
    computed: {
      inputPlaceHolder () {
        return this.video.type === 0 ? '请输入视频地址或选择视频文件' : '请输入实时流地址'
      }
    },
    methods: {
      videoTypeButtonStyle (index) {
        if (this.video.type === index) {
          return {
            'border-bottom': '4px solid #fecb7a'
          }
        } else {
          return {
            'border-bottom': '4px solid white'
          }
        }
      },
      selectFile () {
        document.getElementById('select-file').click()
      },
      getFileAddress (e) {
        if (document.getElementById('select-file').files[0]) {
          this.video.videoAddress = document.getElementById('select-file').files[0].name
        } else {
          this.video.videoAddress = ''
        }
      },
      openVideo () {
        let __this = this
        if (__this.video.videoAddress === '') {
          return
        }
        if (__this.video.videoAddress.includes('/') || __this.video.videoAddress.includes('\\')) {
          __this.request.get('/videoTransform/importVideo')
            .query({
              address: __this.video.videoAddress
            })
            .end(function (err, res) {
              if (err || !res.ok) {
                console.log(err)
                __this.$message({
                  type: 'error',
                  message: '连接服务器失败'
                })
              } else {
                let tmpData = JSON.parse(res.text)
                if (tmpData.result) {
                  if (__this.videoProcess === null) {
                    __this.videoProcess = setInterval(() => {
                      __this.video.randomNum = '?random=' + Math.random()
                    }, 100)
                  }
                } else {
                  clearInterval(__this.videoProcess)
                  __this.videoProcess = null
                  console.log(tmpData.errorMessage)
                }
              }
            })
        } else {
          let file = document.getElementById('select-file').files[0]
          if (file === undefined) {
            return
          }
          __this.importDisabled = true
          if (__this.videoProcess !== null) {
            clearInterval(__this.videoProcess)
            __this.videoProcess = null
          }
          let formData = new FormData()
          /*
          formData.append('name', new Date().Format('yyyyMMddHHmmssS') + '.jpg')
          */
          formData.append('createUser', __this.curUser.userName)
          formData.append('video', file)
          __this.request.post('/videoTransform/uploadVideo')
            .send(formData)
            .end(function (err, res) {
              __this.importDisabled = false
              if (err || !res.ok) {
                console.log(err)
              } else {
                let tmpData = JSON.parse(res.text)
                if (tmpData.result) {
                  if (__this.videoProcess === null) {
                    __this.videoProcess = setInterval(() => {
                      __this.video.randomNum = '?random=' + Math.random()
                    }, 100)
                  }
                } else {
                  console.log(tmpData.errorMessage)
                }
              }
            })
        }
      },
      videoTransform () {
        let __this = this
        if (__this.video.saveInterval <= 5) {
          __this.video.saveInterval = 5
        } else {
          if (__this.imageSaveProcess === null) {
            clearInterval(__this.imageSaveProcess)
            __this.imageSaveProcess = null
          }
          __this.imageSaveProcess = setInterval(function () {
            __this.request.get('/videoTransform/saveImage')
              .query({
                isSave: true,
                createUser: __this.curUser.userName
              })
              .end(function (err, res) {
                if (err || !res.ok) {
                  console.log(err)
                  __this.$message({
                    type: 'error',
                    message: '连接服务器失败'
                  })
                } else {
                  let tmpData = JSON.parse(res.text)
                  if (tmpData.result) {
                    if (__this.imageList.length >= 8) { __this.imageList.pop() }
                    __this.imageList.unshift(tmpData.data)
                  } else {
                    console.log(tmpData.errorMessage)
                  }
                }
              })
          }, 40 * __this.video.saveInterval)
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  .video-transform {
    position: relative;
    .video-list,.video-control{
      height: 50rem;
      width: 40rem;
      position: absolute;
      background-color: white;
      border-radius: 1rem;
      text-align: center;
    }
    .video-control {
      text-align: left;
      height: 50.4rem;
      width: 40.4rem;
      margin: 2rem 0 0 5rem;
      background: url("../assets/video-back.png") no-repeat center;
      background-size: cover;
      .video-type-button {
        border: none;
        color:@blue;
        background: none;
        cursor: pointer;
        outline: none;
        width: 6rem;
        height: 3rem;
        &:nth-of-type(1) {
          margin: 3rem 0 0 3rem;
        }
        &:nth-of-type(2) {
          margin: 0 0 0 2rem;
        }
      }
      .video-address {
        outline: none;
        color: @blue;
        border: none;
        border-bottom: 2px solid @blue;
        margin: 4rem 0 0 3rem;
        width: 22rem;
        padding-left: 0.5rem;
        &::-webkit-input-placeholder{
          color: @blue + #222
        }
      }
      .video-file-button, .video-stream-button {
        height: 2.2rem;
        color: @blue;
        background: none;
        cursor: pointer;
        border: 1px solid @blue;
        &:hover {
          background-color: @blue;
          color: white;
        }
      }
      .video-file-button {
        width: 5rem;
        margin-left: 1rem;
      }
      .video-stream-button {
        width: 9rem;
        margin-left: 2rem;
      }
      .video-display {
        margin: 8rem 0 0 3rem;
        label{
          display: inline-block;
          color: @blue;
        }
        input {
          outline: none;
          border: none;
          border-bottom: 2px solid @blue;
          color: @blue;
          margin: 0 0.5rem 0 0.5rem;
          width: 3rem;
          padding-left: 0.5rem;
        }
        img.video {
          width: 36.8rem;
          height: 20.7rem;
          border: 2px solid black;
          margin: 3.2rem 0 0 -1.5rem;
          background: url("../assets/video.png") no-repeat center;
        }
      }
    }
    .trans-button{
      position: absolute;
      background: url("../assets/trans-normal.png") no-repeat center;
      width: 71px;
      height: 66px;
      background-size: contain;
      margin: 20rem 0 0 47.5rem;
      cursor: pointer;
      &:hover {
        background-image: url("../assets/trans-hover.png");
      }
    }
    .video-list {
      border: 2px solid black;
      margin: 2rem 0 0 55rem;
      label {
        display: inline-block;
        color: @blue;
        text-align: left;
        &.image-title {
          margin-top: 3rem;
          margin-left: 1rem;
          &:nth-of-type(1) {
            width: 18rem;
          }
          &:nth-of-type(2) {
            width:18rem;
          }
        }
        &.image-name, &.image-time {
          margin-top: 1.4rem;
          padding-top: 2rem;
          width: 18rem;
          border-bottom: 2px solid black;
        }
      }
      .image-list {
        margin: 1rem auto;
        width: 38rem;
        height: 42rem;
        overflow-y: auto;
        overflow-x: hidden;
        label {
          padding-left: 1rem;
        }
        &::-webkit-scrollbar {
          width: 1.5rem;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 4rem;
          background: url("../assets/scrollbar.png") no-repeat center;
        }
      }
    }
  }
</style>



