<template lang="pug">
  .index
    .nav
      span {{nav.pages[nav.curPage].name}}
        label
      span(@click="navPage(0)") {{navColumn(0)}}
      span(@click="navPage(1)") {{navColumn(1)}}
      span(@click="navPage(2)") {{navColumn(2)}}
    .main-body
      //button(@click="nav.curPage = (++nav.curPage) % 4") 下一个
      el-collapse-transition
        component(:is="nav.pages[nav.curPage].view", :request="request", :curUser="curUser", :socket="socket")
</template>

<script>
  import UserManage from '@/components/UserManage'
  import ImageSearch from '@/components/ImageSearch'
  import ImageDetect from '@/components/ImageDetect'
  import VideoTransform from '@/components/VideoTransform'
  import superagent from 'superagent'
  /*
  import io from 'socket.io-client'
  const socket = io(':1001')
  socket.on('connect', function () {
    console.log('服务器已连接')
  })
  socket.on('disconnect', function () {
    console.log('连接失败')
  })

  socket.on('hello', function (data, fn) {
    console.log('Hello')
    fn()
  })
  socket.on('new-image', function (data) {
    console.log('new-image')
    console.log(data)
  })
  socket.on('ackevent2', function (data, fn) {
    console.log('ackEvent2')
    fn('001')
  })

  socket.emit('client', {
    userName: 'vts',
    message: '123'
  })
  */
  export default {
    name: 'index',
    props: ['curUser'],
    data () {
      return {
        nav: {
          pages: [
            {
              name: '视频转换',
              view: VideoTransform
            },
            {
              name: '图片检索',
              view: ImageSearch
            },
            {
              name: '图像识别',
              view: ImageDetect
            },
            {
              name: '用户管理',
              view: UserManage
            }
          ],
          curPage: 1
        },
        request: superagent,
        socket: ''
      }
    },
    mounted () {
    },
    methods: {
      navColumn: function (index) {
        let pages = this.nav.pages
        let curPage = this.nav.curPage
        return [].concat(pages.slice(0, curPage), pages.slice(curPage + 1, pages.length))[index].name
      },
      navPage: function (index) {
        let __this = this
        let name = this.navColumn(index)
        let i = this.nav.pages.findIndex(item => item.name === name)
        if (__this.curUser.permission[i]) {
          this.nav.curPage = i
        } else {
          __this.$message({
            type: 'error',
            message: '用户  ' + __this.curUser.userName + '  没有权限'
          })
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  @yellow-hover: #feb747;
  .nav{
    width:100rem;
    color:white;
    margin-top: 2rem;
    text-align: left;
    span {
      display: inline-block;
      cursor: pointer;
      font-size: 1.6rem;
      padding-top: 0.2rem;
      &:nth-of-type(1) {
        margin-left: 4.6rem;
        label {
          display: inline;
          width: 1.2rem;
          height: 2px;
          border-top: 2px solid white;
          position: absolute;
          margin-left: -5.2rem;
          margin-top: -0.3rem;

        }
      }
      &:nth-of-type(2){
        margin-left: 50rem;
      }
      &:nth-of-type(3),&:nth-of-type(4){
        margin-left: 10rem;
      }
    }
  }
</style>
<style lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  @yellow-hover: #feb747;
  .image-pagination {
    overflow: hidden;
    .el-pagination {
      .btn-prev,.btn-next, button.disabled {
        border: none;
        background: none;
        .el-icon {
          display: none;
        }
      }
      .btn-prev {
        background: url("../assets/prev-normal.png") no-repeat 0.5rem 0.45rem;
        &:hover {
          background: url("../assets/prev-hover.png") no-repeat 0.5rem 0.45rem;
        }
      }
      .btn-next {
        background: url("../assets/next-normal.png") no-repeat 0.5rem 0.5rem;
        &:hover{
          background: url("../assets/next-hover.png") no-repeat 0.5rem 0.5rem;
        }
      }
    }
    .el-pager{
      li {
        background-color: @yellow;
        color:white;
        margin-left: 0.5rem;
        border:2px solid black;
        border-radius: 0.5rem;
        text-shadow: 1px 0 1px black, -1px 0 1px black, 0 -1px 1px black, 0 1px 1px black;
        width: 3rem;
        height: 2rem;
        line-height: 1.6rem;
        &.active {
          background-color: #ffaa95;
          border:2px solid black;
          &:hover {
            background-color: #ffaa95;
          }
        }
        &.active+li, &:last-child {
          border:2px solid black;
        }
        &:last-child {
          margin-right: 0.5rem;
        }
        &.btn-quicknext, &.btn-quickprev{
          color:white;
        }
        &:hover {
          background-color:  #feb747;
          color: white;
        }
        &.el-icon-d-arrow-left, &.el-icon-d-arrow-right {
          &:before{
            content: "\e61a"
          }
        }
      }
    }
  }
</style>



