<template lang="pug">
  .image-search
    button.type-button(:style="typeButtonStyle(0)", @click="type=0") 转换时间
    button.type-button(:style="typeButtonStyle(1)", @click="type=1") 识别时间
    br
    label.time-title 起始时间
    label.time-title 结束时间
    br
    input.start-year(v-model.number="startTime.year", type="number")
    label 年
    input.time-month(v-model.number="startTime.month", type="number")
    label 月
    input.time-day(v-model.number="startTime.day", type="number")
    label 日
    input.time-hour(v-model.number="startTime.hour", type="number")
    label :
    input.time-minute(v-model.number="startTime.minute", type="number")
    input.end-year(v-model.number="endTime.year", type="number")
    label 年
    input.time-month(v-model.number="endTime.month", type="number")
    label 月
    input.time-day(v-model.number="endTime.day", type="number")
    label 日
    input.time-hour(v-model.number="endTime.hour", type="number")
    label :
    input.time-minute(v-model.number="endTime.minute", type="number")
    button.search-button(@click="imageSearch") 检索
    .image-list
      span.image-item(v-for="(image, index) in imageList", :key="index")
        img(:src="'/imageSearch/getImage?type=' + type + '&id=' + image.id")
        br
        label {{image.name}}
    .image-pagination
      el-pagination(layout="prev, pager, next", :total="imageNum", :page-size="pageSize", :current-page.sync="curPage", @current-change="imageSearch")
</template>

<script>
  export default {
    name: 'image_search',
    props: ['request', 'curUser', 'socket'],
    data () {
      return {
        type: 0,
        imageNum: 0,
        curPage: 1,
        pageSize: 8,
        startTime: {
          year: 2017,
          month: 1,
          day: 20,
          hour: 12,
          minute: 35
        },
        endTime: {
          year: new Date().getFullYear(),
          month: new Date().getMonth() + 1,
          day: new Date().getDate(),
          hour: new Date().getHours(),
          minute: new Date().getMinutes()
        },
        imageList: []
      }
    },
    watch: {
      type: function () {
        let __this = this
        __this.curPage = 1
        __this.imageNum = 0
        __this.imageList.splice(0)
      }
    },
    methods: {
      typeButtonStyle (index) {
        if (this.type === index) {
          return {
            'border-bottom': '4px solid #fecb7a'
          }
        } else {
          return {
            'border-bottom': '4px solid white'
          }
        }
      },
      imageSearch () {
        let __this = this
        let startTime = new Date(__this.startTime.year, __this.startTime.month - 1, __this.startTime.day, __this.startTime.hour, __this.startTime.minute, 0)
        let endTime = new Date(__this.endTime.year, __this.endTime.month - 1, __this.endTime.day, __this.endTime.hour, __this.endTime.minute, 0)
        if (startTime.getFullYear().isNaN || endTime.getFullYear().isNaN) {
          __this.$message({
            type: 'error',
            message: '时间格式错误'
          })
          return
        }
        startTime = startTime.Format('yyyy-MM-dd HH:mm:ss')
        endTime = endTime.Format('yyyy-MM-dd HH:mm:ss')
        __this.request.post('/imageSearch/search')
          .send({
            jsonString: JSON.stringify({
              type: __this.type,
              index: (__this.curPage - 1) * __this.pageSize,
              pageSize: __this.pageSize,
              startTime: startTime,
              endTime: endTime
            })
          })
          .end(function (err, res) {
            if (err || !res.ok) {
              console.log(err)
              __this.$message({
                type: 'error',
                message: '连接服务器失败'
              })
            } else {
              let tmpData = JSON.parse(res.text)
              if (tmpData.result) {
                if (tmpData.imageNum === 0) {
                  __this.$message({
                    type: 'error',
                    message: '无法查询到数据，请检查相关条件'
                  })
                  __this.curPage = 1
                  __this.imageNum = 0
                  __this.imageList.splice(0)
                  return
                }
                __this.imageNum = tmpData.imageNum
                __this.imageList.splice(0)
                tmpData.imageList.forEach((image) => {
                  let tmpImage = {
                    id: image.id,
                    name: image.name
                  }
                  __this.imageList.push(tmpImage)
                })
              } else {
                console.log(tmpData.errorMessage)
              }
            }
          })
      }
    }
  }
</script>

<style scoped lang="less">
  @blue: #52a3ff;
  @yellow: #fecb7a;
  @yellow-hover: #feb747;
  .image-search {
    position: relative;
    width: 88rem;
    height: 50rem;
    border:2px solid black;
    border-radius: 1rem;
    margin: 2rem 0 0 5rem;
    background-color: white;
    text-align: left;
    color: @blue;
    button{
      cursor: pointer;
      outline: none;
      &.type-button {
        background: none;
        color:@blue;
        width: 7rem;
        height: 3rem;
        border:none;
        &:nth-of-type(1){
          margin: 2rem 0 0 4rem;
        }
        &:nth-of-type(2) {
          margin-left: 2rem;
        }
      }
      &.search-button {
        background-color: @yellow;
        width: 6.8rem;
        height: 2.2rem;
        border: 2px solid black;
        margin-left: 3rem;
        border-radius: 4px;
        color:white;
        text-shadow: 1px 0 2px black, -1px 0 2px black, 0 -1px 2px black, 0 1px 2px black;
        &:hover {
          background-color: @yellow-hover;
        }
      }
    }
    label {
      display: inline-block;
      &.time-title {
        color: @blue + #222;
        &:nth-of-type(1) {
          margin: 2.5rem 0 0 4rem;
        }
        &:nth-of-type(2) {
          margin-left: 32rem;
        }
      }
    }
    input {
      outline: none;
      border: 1px solid @blue;
      color: @blue;
      width: 3rem;
      padding-left: 0.5rem;
      &.time-month, &.time-day, &.time-hour{
        margin: 0 0.2rem 0 2.5rem;
      }
      &.time-minute {
        margin: 0 0 0 0.2rem;
      }
      &.start-year, &.end-year{
        width: 5rem;
      }
      &.start-year {
        margin: 0.8rem 0.2rem 0 4rem;
      }
      &.end-year {
        margin-left: 4rem;
      }
    }
    .image-list {
      margin: 1rem 0 0 1.5rem;
      width: 86.5rem;
      height: 35rem;
      overflow: hidden;
      span.image-item {
        display: inline-block;
        &:nth-of-type(4n+1) {
          margin: 2.2rem 0 0 0.2rem;
        }
        &:not(:nth-of-type(4n+1)) {
          margin: 0 0 0 1.5rem;
        }
        img {
          width: 19.7rem;
          height: 11rem;
          border:1px solid black;
        }
        label {
          margin-top: 0.5rem;
          height: 100%;
          overflow: hidden;
        }
      }
    }
    .image-pagination {
      margin: 1.2rem 0 0 2rem;
      position: absolute;
    }
  }

</style>



